@import"https://fonts.cdnfonts.com/css/montserrat";*{font-family:"Montserrat",sans-serif;box-sizing:border-box;margin:0px}body #root{height:100vh;display:grid;grid-template-columns:repeat(4, 1fr);grid-template-rows:repeat(3, auto);grid-gap:10px}p{font-family:"Open Sans";text-align:justify}.card-container{background-color:#f4f4f4;border-radius:10px}.card-container .title{display:flex;justify-content:space-between;min-height:120px;align-items:center;border-bottom:3px solid #fff;padding:10px 20px 10px 20px}.card-container .title__main{max-width:70%}.card-container .title__main h2{color:#ffd200;font-size:1.3rem}.card-container .title__main h3{color:gray;margin:0px 0px 5px 0px;font-weight:500;font-size:1.1rem}.card-container .title__main h4{color:gray;font-style:italic;font-weight:400;margin:0;font-size:1rem}.card-container .title__details{display:flex;flex-direction:column;align-items:flex-end;justify-content:space-between;height:100%;min-width:19%}.card-container .title__details__dates{font-size:1.1rem;color:gray}.card-container .title__details__place{font-size:1.1rem;text-transform:uppercase;color:gray;margin:0px;text-align:end}@media only screen and (max-width: 768px){.card-container .title{max-height:none;height:100%;padding:0px 20px 15px 20px;flex-direction:column;align-items:flex-start}.card-container .title__main h2{font-size:1rem}.card-container .title__main h3{font-size:.8rem}.card-container .title__main h4{font-size:.8rem}.card-container .title__details{flex-direction:row}.card-container .title__details__dates{margin:0px;font-size:.8rem}.card-container .title__details__dates::after{content:"  •"}.card-container .title__details__place{font-size:.8rem;padding-left:5px}}.card-container .content{width:100%;padding:0px 20px 10px 20px;height:100%}.card-container .content h5{font-size:1rem;margin:10px 0px}.card-container .content__description{font-size:.9rem;margin:0px}.card-container .content__examples{font-size:.9rem;font-style:italic;margin:10px 0px}@media only screen and (max-width: 768px){.card-container .content h5{font-size:.8rem}.card-container .content__description{font-size:.7rem}.card-container .content__examples{font-size:.7rem}}